




















import Vue from "vue";
import Card from "../components/Card.vue";
export default Vue.extend({
    components: {
        Card
    },
    methods: {
        quiz(totalRound: number, type: string) {
            this.$store.commit("setQuizOption", {
                totalRound: totalRound,
                optionCnt: 5,
                type: type
            });
            this.$router.push("/quiz");
        }
    }
});
